.contact {
    height: 30vh;
}

.MuiInputLabel-root {
    color: #FFFFFF !important;
}

.MuiOutlinedInput-root {
    color: #FFFFFF !important;
}

.MuiInput-root {
    border-bottom: 1px solid #BB86FC !important;
    width: 300px;
}

form {
    margin-left: 25%;
    margin-right: 25%;
}

.first-row {
    display: flex;
	justify-content: space-between;
    gap: 50px;
}

.row-space {
    height: 50px;
}

.second-row {
    display: flex;
	justify-content: space-between;
    gap: 200px;
}

@media screen and (max-width: 900px) {
    .contact {
        height: 35vh;
    }

    .MuiInput-root {
        width: 300px;
    }

    .MuiButtonBase-root {
        width: 500px;
    }

	form {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        margin-left: 5%;
        margin-right: 5%;
    }

    .first-row {
        flex-direction: row;
        justify-content: center;
        flex-wrap: wrap;
        gap: 30px;
    }

    .row-space {
        height: 30px;
        display: flex;
        flex-direction: row;
        justify-content: center;
    }

    .second-row {
        flex-direction: row;
        justify-content: center;
        flex-wrap: wrap;
        gap: 30px;
    }
}

@media screen and (min-width: 1800px) {
	.picture {
        height: 90vh;
    }
}