.project-card {
	text-align: left;
	margin: 20px;
	background-color: #17181d;
	position: relative;
	width: 300px;
	height: 370px;
	border: 1px solid #3700B3;
}

.top-border {
	border-top: solid;
	border-width: 7px;
	border-color: #f14d4d;
}

.image-container {
	width: 300px;
	height: 150px;
	display: flex;
	align-items: center;
	overflow: hidden;
}

.image-container img {
    width: 100%;
    height: 100%;
	object-fit: cover;
}

.bottom-container {
	height: 210px;
	width: 300px;
	font-size: 20px;
	overflow: hidden;
	-webkit-transition: all 0.5s linear;
	-moz-transition: all 0.5s linear;
	-o-transition: all 0.5s linear;
	transition: all 0.5s linear;
	position: absolute;
	background-color: #17181d;
	color: #E0E0E0;
	opacity: 1;
}

.scroll-down {
	margin: 20px;
    display: block;
}

.scroll-up {
	margin: 20px;
	font-size: 15px;
	font-weight: 400;
    display: none;
}

.text-container {
	margin: 5px;
	font-size: 15px;
}

.top-container {
    display: flex;
    justify-content: space-between;
    margin-top: -10px;
    text-align: left;
    width: 300px;
    height: 11px;
    overflow: hidden;
    color: #444D56;
    background-color: #E0E0E0;
    font-family: sans-serif;
    position: absolute;
    opacity: 0;
}

.top-container a {
	margin: 20px;
}

.top-title {
	margin: 20px;
	text-transform: uppercase;
	font-size: 16px;
	font-weight: 600;
	z-index: 2;
}

.scroll-title {
	text-align: left;
	text-transform: uppercase;
	font-size: 16px;
	font-weight: 600;
	color: #BB86FC;
}

.project-card:hover .bottom-container{
    margin-top: -100px;
	height: 260px;
    opacity: 1;
}

.project-card:hover .top-container{
    height: 65px;
    opacity: 1;
}

.project-card:hover .scroll-down{
    display: none;
}

.project-card:hover .scroll-up{
    display: block;
}

a {
	color: #333333;
	text-decoration: none;
}

.top-container a:hover {
	color: #8B0000;
}