.about-container {
    text-align: left;
    background-color: #121212;
    color: #E0E0E0;
    font-family: sans-serif;
}

.picture {
    height: 80vh;
    background-image: url('../images/sabin.jpg');
    background-position: center bottom fixed;
    background-repeat: no-repeat;
    background-size: cover;
}

.about-text {
    padding-bottom: 5px;
    font-size: 16px;
    margin-left: 15%;
    margin-right: 15%;
}

.heading {
    text-align: center;
    font-size: 45px;
    color: #BB86FC;
}

.headers {
    font-size: 20px;
    color: #BB86FC;
}

.about-text a {
    color: #BB86FC;
    text-decoration: none;
}

@media screen and (max-width: 768px) {
    .picture {
        height: 50vh;
    }

    .about-text {
        font-size: 15px;
        margin-left: 5%;
        margin-right: 5%;
    }
}

@media screen and (min-width: 1800px) {
    .picture {
        height: 90vh;
    }

    .about-text {
        font-size: 25px;
        margin-left: 25%;
        margin-right: 25%;
    }
}