nav {
	display: flex;
	justify-content: space-between;
	align-items: center;
	position: fixed;
	width: 100%;
	min-height: 5vh;
	font-size: 25px;
	background-color: #17181d;
    color: #FFFFFF;
	font-family: Montserrat, sans-serif;
	z-index: 5;
}

.logo {
	font-weight: 600;
	margin-left: 5%;
	letter-spacing: 3px;
}

.nav-links {
	margin-right: 5%;
	display: flex;
	justify-content: flex-end;
}

.nav-links a {
	font-weight: 300;
	text-decoration: none;
	font-size: 20px;
    /*color: #ffb200;*/
	color: #BB86FC;
}

.nav-links li {
	list-style: none;
}

.nav-links a {
  display: block;
  padding: 1rem 2rem 1rem 2rem;
  border-bottom: 3px solid transparent;
}

.nav-links a:hover {
	border-bottom: 3px solid white;
}

.burger div {
	width: 25px;
	height: 3px;
	background-color: white;
	margin: 5px;
	transition: all 0.3s ease-in;
}
.burger {
	display: none;
	margin-right: 5%;
}

@media screen and (max-width: 768px) {

	body {
		overflow-x: hidden;
	}

	.nav-links {
		position: absolute;
		left: 0px;
		height: 30vh;
		width: 100%;
		top: 6vh;
		background-color: #E0E0E0;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		transform: translateX(-100%);
		transition: transform 0.5s ease-in;
	}

	.nav-links li {
		opacity: 1;
	}

	.nav-links a {
		text-transform: uppercase;
		font-size: 18px;
		font-weight: 600;
		color: #444D56;
	}

	.nav-links a:hover {
		border-bottom: 3px solid #444D56;
	}

	.burger {
		display: block;
		cursor: pointer;
	}

}

.nav-active {
	transform: translateX(0%);
}

@keyframes navlinksFade {
	from {
		opacity: 0;
		transform: translateX(50px);
	}
	to {
		opacity: 1;
		transform: translateX(0px);
	}
}

.toggle .line1 {
	transform: rotate(-45deg) translate(-5px, 6px);
}

.toggle .line2 {
	opacity: 0;
}

.toggle .line3 {
	transform: rotate(45deg) translate(-5px, -6px);
}